.header-color {
  color: #D3D3D3; }

.primary_blue_color {
  color: #0082CD; }

.white {
  color: #F2F2F2; }

.header .sticky {
  position: fixed;
  top: 0;
  border-radius: 0;
  width: 100%;
  z-index: 999;
  background-color: #000000; }

.header .logo-padding {
  padding-left: 65px; }

.header .logo-padding img {
  padding-top: 20px;
  padding-bottom: 20px; }

.header .nav-padding {
  padding: 10px 65px; }

.header .nav li a {
  padding: 30px 15px;
  color: #D3D3D3; }

.header .nav .active a,
.header .nav .active a:hover {
  background-color: #006AA3; }

.footer-details {
  height: auto;
  background-color: #2F2F2F; }
  .footer-details h4 {
    color: #F2F2F2; }
  .footer-details .col-md-4 {
    padding: 30px 40px 30px 30px; }
  .footer-details .padding-left-48 {
    padding-left: 48px; }
  .footer-details .fa-padding {
    padding: 12px; }
  .footer-details .second-div .details-info {
    padding-left: 48px; }
  .footer-details .first-div .details-info {
    padding-left: 42px; }

.footer {
  height: 75px; }
  .footer .footer-fixed {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 23px 69px;
    color: #797979;
    background-color: #262626; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@media only screen and (max-width: 1193px) {
  .openHours {
    display: none; } }



